import React, { useState, useCallback, useEffect } from "react";
import {useDispatch, useSelector} from "react-redux";

import {suggest} from "./appRedux/actions";
import { InstantSearch, MobileSearchBar } from "./components";
import { getInputs } from "./utils";

const MobileInstantSearchWrapper = () => {
  const [enable, setEnable] = useState(false);
  const [enableSearchBar, setEnableSearchBar] = useState(false);

  const dispatch = useDispatch();
  const suggestData = useSelector(({suggest}) => suggest);
  const {products, query, suggestions} = suggestData;

  // handler when user typing
  const handleOnInput = useCallback(e => {
    // Set query
    const q = e.target.value.trim();
    if (!q) {
      return
    }
    dispatch(suggest(q));
  }, [dispatch]);

  // To handle hide mobile instant search dropdown and search bar
  // when user click back button
  const handleOnClickBack = useCallback(() => {
    setEnable(false);
    setEnableSearchBar(false);
  }, []);

  // To handle show mobile instant search dropdown and search bar
  // when user focus on theme's inputs
  useEffect(() => {
    const handleOnInputFocus = e => {
      setEnableSearchBar(true);
      setEnable(true);
    };
    const inputs = getInputs();
    inputs.forEach(input => {
      input.addEventListener("focus", handleOnInputFocus);
    });
    return () => {
      inputs.forEach(input => {
        input.removeEventListener("focus", handleOnInputFocus);
      })
    };
  }, []);

  const showWidget =
    enable &&
    query &&
    (suggestions.length !== 0 || products.length !== 0);

  return (
    <>
      <MobileSearchBar
        onInput={handleOnInput}
        enable={enableSearchBar}
        handleOnClickBack={handleOnClickBack}
      />
      <InstantSearch {...suggestData} enable={showWidget}/>
    </>
  );
};

export default MobileInstantSearchWrapper;
