import React, {useState, useEffect} from "react";
import axios from "axios";
import {Provider} from 'react-redux'
import {ThemeProvider, theme} from "./style";
import GlobalStyles from "./style/GlobalStyles";
import {getInputs} from "./utils";
import InstantSearchWrapper from "./InstantSearchWrapper";
import {Box} from "./components";
import MobileInstantSearchWrapper from "./MobileInstantSearchWrapper";
import config from "./config";
import configureStore from './appRedux/store';

const mobileStore = configureStore();

function App() {
  const [instantSearchs, setInstantSearchs] = useState([]);

  // Get token from js file
  useEffect(() => {
    window.addEventListener('load', function () {
      const scripts = document.getElementsByTagName('script');
      for (let script of scripts) {
        if (script.src && script.src.indexOf(config.scriptIdentifier) > -1) {
          const url = new URL(script.src);
          const params = new URLSearchParams(url.search);
          axios.defaults.headers.common['authorization'] = "Bearer " + params.get("tk");
        }
      }
    });
  }, []);

  useEffect(() => {
    const inputs = getInputs();
    setInstantSearchs(inputs.map((input, index) => {
      const store = configureStore();
      return (
        <Provider store={store}>
          <InstantSearchWrapper key={index} input={input}/>
        </Provider>
      );
    }));
  }, []);

  return (
    <ThemeProvider theme={theme}>
      <GlobalStyles/>
      <Box id="flashsearch-instant-search">
        {instantSearchs}
      </Box>
      <Box id="flashsearch-instant-search-mb">
        <Provider store={mobileStore}>
          <MobileInstantSearchWrapper/>
        </Provider>
      </Box>
    </ThemeProvider>
  );
}

export default App;
